/* I E   S T Y L E S */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .block {
    width: 100%; } }

.ov-hidden {
  overflow: hidden; }

.close:not(:disabled):not(.disabled) {
  cursor: pointer; }

@media screen and (min-width: 992px) {
  .btn:hover {
